/* tslint:disable:max-line-length */

import { ITelegram } from 'telegram-js';

export function addPublicKeys(telegram: ITelegram) {
  let publicKeys = [{
    fingerprint: '0x9a996a1db11c729b',
    modulus: '',
    exponent: '010001',
  },
    {
      fingerprint: '0xb05b2a6f70cdea78',
      key: '',
      exponent: '010001',
    },
    {
      fingerprint: '0xc3b42b026ce86b21',
      modulus: '',
      exponent: '010001',
    },
    {
      fingerprint: '0x71e025b6c76033e3',
      modulus: '',
      exponent: '010001',
    }];

  publicKeys.forEach(key => telegram.addPublicKey(key));
}



